body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.slider_images{
      display:block;
      z-index: -1;
      margin:7% 15px;
}
.div{
      display:inline-block;
      width:60%;
      margin-top: 10px;
      margin-right: 10%;
      position:relative;
}
.div:after{
      width:100%;
      content:"";
      position:absolute;
      top: 0;
      right: 0;
      height: 95%;
      background:linear-gradient(to right, rgba(255, 255,255, 1), rgba(0,0,0,0))

} 
.slider{
      margin-left: 50%;
}

.footer_content{
      position:fixed;
      bottom: 0;
      width: 100%;
      height: 95px;
      background-color: #ffffff ;
      z-index:-1 ;
}
.linea_horizontal{
      position:fixed;
      bottom:5rem;
      border:#7363f7;
      z-index:1 ;
      background-color:#7363f7;
      height: 2px;
      width: 100%;
}
.img{
      display: inline-block;
      margin-left:25px;
}
.title{
      position:absolute;
      font-style:italic;
      text-align:center;
      bottom:0;
      font-size:30px;
      left: 120px;
      color:#000000;
      text-justify:center;
}

